import { useState } from "react";

const NF = () => {
  const [inp, sInp] = useState();
  const [da, sDa] = useState();
  const [cl, sCl] = useState("black");
  const hanleInput = (e) => {
    e.preventDefault();
    sInp(e.target.value);
  };
  const handlebtn = (e) => {
    sDa(inp);
    sCl("yellow");
  };
  return (
    <div>
      <input
        type="text"
        name="name"
        value={inp}
        onChange={(e) => hanleInput(e)}
        placeholder="Please enter input"
      />
      <button type="submit" onClick={(e) => handlebtn(e)}>
        Submit
      </button>
      <h1 style={{ color: cl }}>{da && da}</h1>
    </div>
  );
};
export default NF;

